/* EventDetails.css */

.event-details-overlay {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: flex-end; /* Align items to the bottom */
  position: fixed;
  bottom: 0; /* Initially set bottom position to the viewport bottom */
  left: 0;
  width: 100%;
  height: 0; /* Initially set height to 0 */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  overflow-y: auto; /* Allow vertical scrolling if content overflows */
  transition: height 0.3s ease; /* Apply transition effect to height property */
}

.event-details-content {
  position: relative;
  width: 70vw;
  background-color: rgba(255, 255, 255, 0.9);
  border: 2px solid rgb(167, 195, 255);
  padding: 20px; /* Add padding for better appearance */
  margin-bottom: 20px; /* Add margin at the bottom to avoid overlapping with close button */
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  opacity: 1; /* Initially show the content */
  transition: opacity 0.3s ease; /* Apply transition effect to opacity property */
}

/* Close button styles */
.close-button {
  position: absolute;
  top: 10px; /* Adjust top position */
  right: 10px; /* Adjust right position */
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: rgba(170, 170, 170, 0.5);
  border: 2px solid rgb(167, 195, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: rgb(167, 195, 255);
  cursor: pointer;
  transition: color 0.3s ease, border-color 0.3s ease;
}

.close-button:hover {
  color: white;
  border-color: white;
}

.spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
}

/* Ripple effect */
.ripple {
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  animation: rippleEffect 0.4s linear;
}

@keyframes rippleEffect {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

/* Show and hide animation */
.event-details-overlay.show {
  height: 100%; /* Expand the height to 100% to reveal the content */
}

.event-details-overlay.hide {
  height: 0; /* Collapse the height to 0 to hide the content */
}

