/* EventList.css */



.button {
  width: calc(100% - 20px); /* Account for margin on both sides */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-right: 10px;
  margin-left: 10px;
  position: relative;
  overflow: hidden;
}

.ripple {
  position: absolute;
  background-color: rgba(0, 102, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  animation: rippleEffect 0.7s linear;
}

@keyframes rippleEffect {
  to {
    transform: scale(5);
    opacity: 0;
  }
}



.event-list-button {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.event-name,
.event-details,
.closing-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.event-name {
  font-weight: bold;
}

.spinner{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999; /* Set a higher z-index to make sure it's above other elements */
}






